import {React} from 'react'
import {Link} from 'react-router-dom'
function Aside() {
    
    return (
        <aside className="border rounded mr-5 c4">
            <ul className="list-group">
               <li className="list-group-item">
                   <Link>Content1</Link>
               </li>
               <li className="list-group-item"><Link>Content2</Link></li>
               <li className="list-group-item"> <Link>Content3</Link></li>
               <li className="list-group-item"> <Link>Content4</Link></li>
               <li className="list-group-item"> <Link>Content5</Link></li>
            </ul>
        </aside>
    )
}

export default Aside
