import React from 'react'
import {Card} from 'react-bootstrap'
import './style.css'
function Cards() {
    return (
        <Card style={{width:'12rem'}}  className=" shadow">
            <Card.Body className="px-2">
                <Card.Title className="bg-light rounded text-center text-success">
                    CARD
                </Card.Title>
                <Card.Text className="bg-white">
                <p>Lorem ipsum dolor sit, amet consectetur adipisicing elit. Maxime, accusantium?</p>
                </Card.Text>
            </Card.Body>
        </Card>
    )
}

export default Cards;

