import React from 'react'
import Aside from './Aside'
function Main(props) {
    return (
        <main className="d-flex justify-content-start container-fluid pt-0 pl-0 m-0 c2 ">
            <Aside/>
            
                <div className="container d-flex justify-content-between c1 mt-5">
                    {
                        props.children
                    }
                </div>
                
            
        </main>
    )
}

export default Main
